import React from "react";
import "../App.css";
import Card from "./Card";
import contacts from "./contacts";

function App() {
  return (
    <>
      <h1>My Contacts</h1>
      {contacts.map((c) => {
        return (
          <Card
            name={c.name}
            imageURL={c.imageURL}
            phone={c.phone}
            email={c.email}
          />
        );
      })}
    </>
  );
}

export default App;
