import React from "react";

function Card(props) {
  return (
    <>
      <div className="card">
        <div className="top">
          <div className="name">{props.name}</div>
          <img className="circle-img" src={props.imageURL} alt="avatar_img" />
        </div>
        <div className="bottom">
          <p className="info">{props.phone}</p>
          <p className="info">{props.email}</p>
        </div>
      </div>
    </>
  );
}

export default Card;
